import {
	mapState
} from 'vuex';
import axios from '@/utils/uniAxios.js'
import {
	uniSearchBar
} from "@/components/uni-search-bar/uni-search-bar.vue";
import {
	uniPopup
} from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniSearchBar,
		uniPopup
	},
	data() {
		return {
			code: null,
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			isCanUse: false, //默认为true
			skuList:[],
			productList: [],
			homeFlashTime: '00',
			newProductList: [],
			productCategoryList: [],
			school: {}
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	onLoad() {
		if (!uni.getStorageSync('hasLogin')) {
			uni.hideTabBar();
			this.$refs.popup.open();
		} else {
			uni.showTabBar();
			this.wxGetUserInfo();
		}
	},
	onShow() {
		this.code = uni.getStorageSync('code');
		this.school = uni.getStorageSync('school') || {};
		this.getHomeList();
	},		
	methods: {
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (infoRes) => {
					this.login();
					if(uni.getStorageSync('userPhone')){
						infoRes.userInfo.phone = uni.getStorageSync('userPhone')
					}
					this.$store.commit('login', infoRes.userInfo);
				}
			});
		},
		//登录
		login() {
			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: (loginRes) => {
					let code = loginRes.code;
					this.code = code;
					uni.setStorageSync('code', code);
				}
			})
		},
		getPhoneNum(e) {
			uni.showLoading({
				title: '登录中...',
				mask: true
			});
			const {encryptedData, iv} = e.detail
			axios.post('/sso/user/getOpenId', {code: this.code}).then(({data}) => {
				const {city, gender, avatarUrl, nickName = 'user'} = this.userInfo;
				const {openId, session_key} = data.data;
				//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
				axios.post('sso/user/getPhoneNum', {encrypdata: encryptedData, ivdata: iv, openId, sessionKey: session_key}).then(resp => {
					console.log(resp, 'resp');
					let phone;
					if (resp.data.code === 200) {
						phone = resp.data.data && resp.data.data.phone;
						this.userInfo.phone = phone;
						this.$store.commit('login', this.userInfo);
						uni.setStorageSync('userPhone',phone)
					}
					axios.post('/sso/user/miniLogin', {city: city || '武汉', gender, icon: avatarUrl, nickname: nickName,
						  "wxAppid": "wx35cb9f6acb94bd15", phone,
						  "wxOpenid": openId
						}).then((res) => {
							const response = res.data;
							if (response.code == 200) {
								uni.setStorageSync('hasLogin', true);
								//openId、或SessionKdy存储//隐藏loading
								uni.setStorageSync('gt', response.data.token);
								uni.hideLoading();
								uni.showTabBar();
								this.close();
							}
						})
				})
				
			})
		},
		// 手机号登录
		toPhone() {
			uni.redirectTo({
				url: '/pages/public/login'
			})
		},
		close() {
			this.$refs.popup.close();
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		navToPosition() {
			uni.navigateTo({
				url: `/pages/index/selectPosition`
			})
		},
		navToMessage() {
			uni.navigateTo({
				url: '/pages/message/center'
			})
		},
		navToSearch() {
			uni.navigateTo({
				url: `/pages/product/list`
			})
			// uni.navigateTo({
			// 	url: `/pages/index/search`
			// })
		},
		navToCate(categoryId = '') {
			uni.setStorageSync('categoryId', categoryId);
			uni.switchTab({
				url: `/pages/category/category`
			})
		},
		getHomeList() {
			axios.post('/home/list', {}).then(({
				data
			}) => {
				if (data.code === 200) {
					this.carouselList = data.data.advertiseList || [];
					this.productCategoryList = data.data.productCategoryList || [];
					this.skuList = data.data.hotProductList || [];
					this.newProductList = data.data.newProductList || [];
					this.productList = data.data.homeFlashPromotion.productList || [];
					// if (data.data.homeFlashPromotion.startTime) this.homeFlashTime = new Date(data.data.homeFlashPromotion.startTime)
					// 	.getHours() - 8;
				}
			})
		}
	}
}
